@import "./Fonts.scss";

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none;
}

a {
  color: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; //10px/16px = 62.5%, 1rem = 10px;
  scroll-behavior: smooth;
}

//for mobile to take 100% height
html,
body {
  height: 100%;
  font-family: $fontRubik;
  overflow-x: hidden;
}

div#root {
  height: 100%;
}
